/*
  6. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
    font: inherit;
}
/*
  8. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root,
#__next {
    isolation: isolate;
    height: 100%;
}

.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.wrapper footer {
    margin-top: auto;
    padding: 40px;
    text-align: center;
}

form h1 {
    text-align: center;
    margin-bottom: 8px;
}

fieldset {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.alert {
    background-color: #d81b60;
    color: white;
    padding: 16px 32px;
    text-align: center;
    margin-bottom: 16px;
}
